//作业
import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/myHeader';
import routes from './router.config';
import {Button,TabBar} from 'antd-mobile'
// import Mytab from './Taobao/Mytab';
import Home from './Taobao/Home';
import Cart from './Taobao/Cart';
import List from './Taobao/List';
import Search from './Taobao/Search'
import Store from './Taobao/Store'
import Service from './Taobao/Service'
import Collect from './Taobao/Collect'
import Detailshow1 from './Taobao/Detailshow1'
import Detailshow2 from './Taobao/Detailshow2'
import Detailshow3 from './Taobao/Detailshow3'
import Detailshow4 from './Taobao/Detailshow4'
import Detailshow5 from './Taobao/Detailshow5'
import Detailshow6 from './Taobao/Detailshow6'
import Detailshow7 from './Taobao/Detailshow7'
import Detailshow8 from './Taobao/Detailshow8'
import Detailshow9 from './Taobao/Detailshow9'
import Detailshow10 from './Taobao/Detailshow10'
//import Detailmytab from './Taobao/Detailmatab'

// https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg
const App = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/list' component={List}/>
			<Route path='/search' component={Search}/>
			<Route path='/store' component={Store}/>
            <Route path='/service' component={Service}/>
            <Route path='/collect' component={Collect}/>
			<Route path='/detailshow1' component={Detailshow1}/>
			<Route path='/detailshow2' component={Detailshow2}/>
			<Route path='/detailshow3' component={Detailshow3}/>
			<Route path='/detailshow4' component={Detailshow4}/>
			<Route path='/detailshow5' component={Detailshow5}/>
			<Route path='/detailshow6' component={Detailshow6}/>
			<Route path='/detailshow7' component={Detailshow7}/>
			<Route path='/detailshow8' component={Detailshow8}/>
			<Route path='/detailshow9' component={Detailshow9}/>
			<Route path='/detailshow10' component={Detailshow10}/>

			{/* <Route component={Home} /> */}
			<Route render={()=><Redirect to='/'/>} />
			
		</Switch>
		{/* <Mytab /> */}
		
	</Router>
	
}
const RouteWithSubRoutes=(props)=>{
	return <Route
	path={props.path}
	render={
		()=><props.component routes={props.routes}/>
	}/>
}
export default App;
export {RouteWithSubRoutes};


