import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Detailmytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="store"
                        icon={
                            <i className='iconfont icon-taobaodianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaodianpu'></i>                           
                        }
                        selected={ pathname === '/store' }
                        onPress={ () => {
                            this.props.history.push('/store');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        title="客服"
                        key="service"
                        selected={ pathname === '/service' }
                        onPress={ () => {
                            this.props.history.push('/service')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-tubiaozhizuomoban-01'></i>
                        }
                        selectedIcon={
                            
                            <i className="iconfont icon-tubiaozhizuomoban-01"/>
                         }
                        title="收藏"
                        key="collect"
                        selected={ pathname==="/collect" }
                        onPress={ () => {
                            this.props.history.push('/collect')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={<div class='jiarudouwuche'><span class='jiarugouwuche-title'>加入购物车</span></div> }
                        selectedIcon={ 
                            <div class='jiarudouwuche'><span class='jiarugouwuche-title'>加入购物车</span></div>
                         }
                        // title="我的淘宝"
                        // key=""
                        // selected={ this.state.selectedTab === 'yellowTab' }
                        // onPress={ () => {
                        //     this.setState({
                        //         selectedTab: 'yellowTab',
                        //     });
                        // } }
                    >                   
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<div class='lijigoumai'><span class='lijigoumai-title'>立即购买</span></div> }
                        selectedIcon={ 
                            <div class='lijigoumai'><span class='lijigoumai-title'>立即购买</span></div>
                         }
                        // title="我的淘宝"
                        // key=""
                        // selected={ this.state.selectedTab === 'yellowTab' }
                        // onPress={ () => {
                        //     this.setState({
                        //         selectedTab: 'yellowTab',
                        //     });
                        // } }
                    >                   
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Detailmytab);